const btnOpen = document.querySelector(".b-card__btn");
const cont = document.querySelector(".b-card__cont");
console.log(btnOpen);
console.log(cont);
cont.addEventListener("click", function() {
  let target = event.target;
  if (target.classList.contains("b-card__btn")) {
    const parentBtn = target.parentElement;
    parentBtn.classList.add("b-card-hov");
    const cardHov = document.querySelector(".b-card--bg1-hov");
    const hover = document.querySelector(".b-card__hover");
    hover.appendChild(cardHov);
    // function copyHTML() {}
  }

  // const cardHov = document.querySelector(".b-card--bg1-hov");
  // const hover = document.querySelector(".b-card__hover");
  // console.log(cardHov);
  // console.log(hover);
});
